<template>

      <div class="dialog-wrap">
        <div class="dialog-cover" @click="closeMySelf"  v-if="isShow"></div>
        <transition name="drop">
          <div class="dialog-content" v-if="isShow">
            <p class="dialog-close" @click="closeMySelf">x</p>
            <slot>empty</slot>
          </div>
        </transition>
      </div>

</template>

<script>
    export default {
      props:{
          isShow:{
              type:Boolean,
              default:false
          }
      },
      methods:{
        closeMySelf(){
            this.$emit('on-close')
        }
      }
    }
</script>

<style scoped>
  .drop-enter-active {
    transition: all .5s ease;
  }
  .drop-leave-active {
    transition: all .3s ease;
  }
  .drop-enter {
    transform: translateY(-500px);
    transform: scale(1.5,1.5)

  }
  .drop-leave-active {
    transform: translateY(-500px);
  }

  .dialog-wrap {
    position: fixed;
    width: 100%;
    height: 100%;
  }
  .dialog-cover {
    background: #000;
    opacity: .3;
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .dialog-content {
    width: 50%;
    position: fixed;
    /*max-height: 50%;*/
    overflow: auto;
    background: #fff;
    top: 20%;
    left: 50%;
    margin-left: -25%;
    z-index: 10;
    padding: 2%;
    line-height: 1.6;
    box-shadow:0px 0px 5px 5px #00a971 ;
  }
  .dialog-close {
    position: absolute;
    right: 5px;
    top: 5px;
    width: 20px;
    height: 20px;
    text-align: center;
    cursor: pointer;
  }
  .dialog-close:hover {
    color: #4fc08d;
  }
</style>



