<template>
    <div >
      <div class="index-wrap">
        <div class="index-left">
          <div class="index-left-block">
            <h2 >全部产品</h2>
          <!--数据渲染，生成列表，循环直接套用template-->
          <template v-for="product in productList">
            <h3>{{product.title}}</h3>
            <ul>
              <li v-for="item in product.list">
                <a :href="item.url">{{item.name}}</a>
                <!--数据添加hot，确定那个是热点-->
                <span v-if="item.hot" class="hot-tag">HOT</span>
              </li>
            </ul>
            <div v-if="!product.last" class="hr"></div>
          </template>
          </div>
          <img src="http://img.mukewang.com/596440630001f1ad12000460.jpg" alt="" style="width: 270px;height: 120px;padding: 10px 15px">

          <div class="index-left-block lastest-news">
            <h2>最新消息</h2>
            <ul>
                <li v-for="item in newsList">
                  <a :href="item.url" class="new-item">{{ item.title }}</a>
                </li>
            </ul>
          </div>
        </div>
        <div class="index-right">
          <!--:slides="slides"展现的内容-->
          <slide-show :slides="slides" :inv="slideSpeed" @onChange="doSomeOnSlideChange"></slide-show>
          <div class="index-board-list">
            <!--动态添加class，如果是奇数，就添加line-last；通过数据item.id去选不同的class，添加背景图-->
            <div  class="index-board-item"
                  v-for="(item, index) in boardList"
                  :class="[{'line-last' : index % 2 !== 0}, 'index-board-' + item.id]"
            >
              <div class="index-board-item-inner"></div>
              <h2>{{item.title}}</h2>
              <p>{{item.description}}</p>
              <div class="index-board-button">
                <a :href="item.url" class="button">立即购买</a>
              </div>
            </div>
          </div>
         </div>
      </div>
    </div>
</template>

<script >
  import slideShow from '../components/slideShow'

  export default {

      components:{
        slideShow
      },
      created:function () {
        this.$http.get('api/getNewsList').then(
            function (suc) {
              this.newsList = suc.data
            },function (err) {
              console.log(err)
            }
        )
      },
      methods:{
        doSomeOnSlideChange(){}
      },
      data () {
          return {
            slideSpeed:2000,
            slides: [
                /*只有写了require,webpack才能解读*/
              {
                src: require('../assets/slideShow/pic1.jpg'),
                title: 'xxx1',
                href: 'detail/analysis',
                cid:1
              },
              {
                src: require('../assets/slideShow/pic2.jpg'),
                title: 'xxx2',
                href: 'detail/count',
                cid:2
              },
              {
                src: require('../assets/slideShow/pic3.jpg'),
                title: 'xxx3',
                href: 'detail/publish',
                cid:3
              },
              {
                src: require('../assets/slideShow/pic4.jpg'),
                title: 'xxx4',
                href: 'detail/forecast',
                cid:4
              }
            ],
            boardList: [
              {
                title: '数据统计',
                description: '历史资料、科学实验、检验、统计等所获得的和用于科学研究、技术设计、查证、决策等的数值加以统计为解决方案做前期准备。',
                id: 'car',
                toKey: 'count',
                saleout: false,
                url :'/detail/count '
              },
              {
                title: '数据预测',
                description: ' 关于预测分析，你想了解的全部，你的生活以及这个世界会因为预测分析改变到什么程度，《大数据预测》都会告诉你,你想要的都在这里。',
                id: 'earth',
                toKey: 'forecast',
                saleout: false,
                url :'/detail/forecast'
              },
              {
                title: '流量分析',
                description: '专业的网站访问统计分析报告对网络营销的价值，正如专业的财务分析报告对企业经营策略的价值，其价值远高于原始数据资料。',
                id: 'loud',
                toKey: 'analysis',
                saleout: true,
                url :'/detail/analysis'
              },
              {
                title: '广告发布',
                description: '广告活动按照广告计划执行，到完成广告创作并形成广告作品之后，经过广告主的最后审核同意，即可送到预定的媒介发布刊播。',
                id: 'hill',
                toKey: 'publish',
                saleout: false,
                url :'/detail/publish'
              }
            ],
            newsList:[
            ],
           productList:{
             pc:{
               title:'pc产品',
               list:[
                 {
                   name: '数据统计',
                   url: 'http://starcraft.com'
                 },
                 {
                   name: '数据预测',
                   url: 'http://warcraft.com'
                 },
                 {
                   name: '流量分析',
                   url: 'http://overwatch.com',
                   hot: true
                 },
                 {
                   name: '广告发布',
                   url: 'http://hearstone.com'
                 }

               ]
             },
             app:{
               title:'手机应用类',
               last:true,
               list:[
                 {
                   name: '91助手',
                   url: 'http://weixin.com'
                 },
                 {
                   name: '产品助手',
                   url: 'http://twitter.com',
                   hot: true
                 },
                 {
                   name: '智能地图',
                   url: 'http://maps.com'
                 },
                 {
                   name: '团队语音',
                   url: 'http://phone.com'
                 }
               ]
             }
           }
        }
      }
    }
</script>

<style scoped>
  .index-wrap {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
  }
  .index-left {
    float: left;
    width: 300px;
    text-align: left;
  }
  .index-right {
    float: left;
    width: 900px;
  }
  .index-left-block {
    margin: 15px;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
  }
  .index-left-block .hr {
    margin-bottom: 20px;
  }
  .index-left-block h2 {
    background: #4fc08d;
    color: #fff;
    padding: 10px 15px;
    margin-bottom: 20px;
  }
  .index-left-block h3 {
    padding: 0 15px 5px 15px;
    font-weight: bold;
    color: #222;
  }
  .index-left-block ul {
    padding: 10px 15px;
  }
  .index-left-block li {
    padding: 5px;
  }
  .index-board-list {
    overflow: hidden;
  }
  .index-board-item {
    float: left;
    width: 400px;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
    padding: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
  }
  .index-board-item-inner {
    margin-right: 20px;
    float: left;
    min-height: 125px;
    padding-left: 120px;
  }
  .index-board-car .index-board-item-inner{
    background: url(../assets/images/1.png) no-repeat;
  }
  .index-board-loud .index-board-item-inner{
    background: url(../assets/images/2.png) no-repeat;
  }
  .index-board-earth .index-board-item-inner{
    background: url(../assets/images/3.png) no-repeat;
  }
  .index-board-hill .index-board-item-inner{
    background: url(../assets/images/4.png) no-repeat;
  }
  .index-board-item h2 {
    font-size: 18px;
    font-weight: bold;
    color: #000;
    margin-bottom: 15px;
  }
  .line-last {
    margin-right: 0;
  }
  .index-board-button {
    margin-top: 20px;
  }
  .lastest-news {
    min-height: 512px;
  }
  .hot-tag {
    background: red;
    color: #fff;
  }
  .new-item {
    display: inline-block;
    width: 230px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



