<template>

      <div class="login-form">
        <div class="g-form">
          <div class="g-form-line">
            <span class="g-form-label">用户名：</span>
            <div class="g-form-input">
              <input type="text"
                     placeholder="请输入用户名"
                     v-model="usernameModel"
              >
            </div>
            <span class="g-form-error">{{userErrors.errorText}}</span>
          </div>
          <div class="g-form-line">
            <span class="g-form-label">密码：</span>
            <div class="g-form-input">
              <input type="password"
                     placeholder="请输入密码"
                     v-model="passwordModel"
              >
            </div>
            <span class="g-form-error">{{ passwordErrors.errorText }}</span>
          </div>
          <div class="g-form-line">
            <div class="g-form-btn">
              <a class="button" @click="onLogin">登录</a>
            </div>
          </div>
          <p>{{ errorText }}</p>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
              usernameModel:'',
              passwordModel:'',
              errorText: ''
            }
        },
      methods:{
        onLogin () {
          if (!this.userErrors.status || !this.passwordErrors.status) {
            this.errorText = '部分选项未通过'
          }
          else {
            this.errorText = ''
            this.$http.get('api/login')
              .then((res) => {
                this.$emit('has-log', res.data)
              }, (error) => {
                console.log(error)
              })
          }
        }
      },

      computed:{
         userErrors(){
             /*用户名渲染*/
            let errorText,status
            if(!/@/g.test(this.usernameModel)) {
              status=false
              errorText='不包含@'
            }else{
              status = true
              errorText= ''
            }
            /*检测，第一次进来是空，然后变为真，就在也进步来*/
           if (!this.userFlag) {
             errorText = '',
             this.userFlag = true
           }
            return{
              status,
              errorText
            }
         },
        passwordErrors () {
            /* 密码验证*/
          let errorText, status
          if (!/^\w{1,6}$/g.test(this.passwordModel)) {
            status = false
            errorText = '密码不是1-6位'
          }
          else {
            status = true
            errorText = ''
          }
          /*第一进来为空，之后设为true，再也进不来，页面没有渲染，不用声明,解决开始就报错的问题*/
          if (!this.passwordFlag) {
            errorText = '',
            this.passwordFlag = true
          }
          return {
            status,
            errorText
          }
        }
      }
    }
</script>

<style>

</style>



